$primary-color: #ffe400;//#86c232;
$secondary-color: #f2f2f2;

$background-primary: #474b4f;
$background-secondary: #222629;

body {
    margin: 0;
    padding: 0;
    background: url('/assets/images/bg.jpg');
    background-attachment: fixed;
    color: rgba($color: $secondary-color, $alpha: 1.0);
    font-family: 'Roboto', sans-serif;
    a {
        color: rgba($color: #ffe400, $alpha: 1.0);
    }
    a:hover {
        color: rgba($color: #ffe400, $alpha: 0.9);
    }

    h1 {
        color: rgba($color: $primary-color, $alpha: 1.0);
    }
}

body::before {
    content: "";
    background-color: rgba($color: $background-primary, $alpha: 0.8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: -1;
}


//==========================
/* CSS FOR NAVIGATION BAR */
nav {
    box-shadow: 0px 4px 4px 0px #080808;
    margin-bottom: 16px;
    margin-top: 16px;
    background-color: rgba($color: $background-secondary, $alpha: 1.0);


    a:hover {
        text-decoration: underline;
        color: rgba($color: $primary-color, $alpha: 1.0);
    }

    .float-right {
        a {
            padding: 0em 0.6em;
            color: rgba($color: $primary-color, $alpha: 1.0);
            font-size: 18px;
        }

        a:nth-child(2) {
            border-right: 1px solid white;
            border-left: 1px solid white;
            padding: 0em 0.8em;
        }

        a:hover {
            color: rgba($color: $secondary-color, $alpha: 1.0);
        }
    }
}

.navbar {
    padding-left: 2.5em;
    padding-right: 2.5em;
}

.navbar-brand {
    font-weight: bold;
    font-size: 36px;
    color: rgba($color: $secondary-color, $alpha: 1.0);
}



//================
/* CSS FOR HOME */
.abt {

    font-size: 18px;
    line-height: 24px;
    width: auto;
    
    margin-top: 32px;

    h1 {
        font-size: 36px;
    }

    a {
        text-decoration: none;
        color: $secondary-color;
    }

    i {
        text-decoration: underline;
    }

}

.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 300px;
    margin: auto;
    text-align: center;

    background: rgba($color: $background-primary, $alpha: 0.8);

    h1 {
        margin-top: 16px;
    }

    .title {
        color: rgba($color: $primary-color, $alpha: 0.8);
        font-size: 18px;
      }
      
      button {
        border: none;
        outline: 0;
        display: inline-block;
        padding: 8px;
        color: white;
        background-color: rgba($color: $background-secondary, $alpha: 1.0);
        text-align: center;
        cursor: pointer;
        width: 100%;
        font-size: 16px;
      }
      
      a {
        text-decoration: none;
        font-size: 22px;
        color: rgba($color: $secondary-color, $alpha: 1.0);
        display: inline;
        }
      
        .social-icons {
            margin-bottom: 16px;

            a {
                margin: 8px;
            }
        }

        .info {
            margin: 0;
        }
      button:hover, a:hover {
        opacity: 0.7;
      }
}

@media(min-width: 599px) {
    .about {
        display: flex;
    }
    
}

//==============
/* CSS FOR BLOG */

.blog {

    margin-top: 32px;

    h1 > a {
        color: rgba($color: $primary-color, $alpha: 1.0);
        font-size: 36px;
    }

    p {
        font-size: 18px;
    }
}


//================
/* CSS FOR POSTS */

.post {
    #author {
        border-bottom: 1px dashed $secondary-color;
    }
}


//====================
/* CSS FOR PROJECTS */
.jumbotron {
    background-color: rgba($color: $background-secondary, $alpha: 0.4);

    .btn {
        border: 1px solid rgba($color: $primary-color, $alpha: 1.0);
        border-radius: 8px;
        color:  rgba($color: $secondary-color, $alpha: 1.0);
    }

    .my-4 {
        background-color: $primary-color;
    }

}

//==========================================================================
/*                              M  O   B   I   L   E                      */
//==========================================================================
@media(max-width: 500px) {
    
    //==========================
    /* css for navigation bar */
    .navbar {
        
        .float-right {
            margin-left: auto;
            margin-right: auto;

            a {
                font-size: 16px;
            }
            
        }   
    }

    .navbar-brand {
        display: none;
    }


    //======================
    /* css for home page */
    .abt {
        line-height: 20px;
        
        p, li {
            font-size: 16px;
        }

        h1 {
            font-size: 32px;
        }
    }


    @media(max-width: 499px) {
        .card {
            transform: scale(0.9);
        }
    }
    
    //================
    /* css for blog */
    .blog {

        margin-top: 16px;

        h1 > a {
            font-size: 32px;
        }
    
        p {
            font-size: 16px;
        }
    }
    

    //=================
    /* css for posts */
    .post {
        font-size: 16px;

        h1 {
            font-size: 32px;
        }
    }


    //============
    /* Projects */

    .jumbotron {

        .display-5 {
            font-size: 32px;
        } 
        .lead {
            font-size: 14px;
        }

        .btn {
            transform: scale(0.7);
        }
    }

    .card {
        font-size: 14px;
    }
    
}
